import { Component, OnInit } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";

import { Observable } from "rxjs";

import { TicketType } from "src/app/modelos/tickettype.model";
import { CacheStorageService } from "src/app/services/cachestorage.service";
import { TicketService } from "../../services/ticket.service";
import { FormGroup, FormBuilder, Validators,AbstractControl } from "@angular/forms";
import { RegisterResponse } from "src/app/modelos/Response/RegisterResponse.model";

@Component({
  selector: "app-nuevoticket",
  templateUrl: "./nuevoticket.component.html",
  styleUrls: ["./nuevoticket.component.css"]
})
export class NuevoticketComponent implements OnInit {
  ticketType: TicketType[];
  formularioNewTike: FormGroup;
  tiket:any = {};
  mensaje: any = [];
  constructor(

    private ticketservice: TicketService,
    private router: Router,
    private cacheManager: CacheStorageService,
    public fb: FormBuilder
  ) {}

  ngOnInit() {
    this.cacheManager.getData<TicketType>("TicketType", (array : TicketType[]) => { this.ticketType = array; });

    this.formularioNewTike = this.fb.group({
      Subject: ['', Validators.compose([Validators.required, Validators.minLength(5), Validators.maxLength(100)])],
      Body : ['', Validators.compose([Validators.required, Validators.minLength(10), Validators.maxLength(500)])],
      SelectTypeTique: ['', [Validators.required]]
    });
  }



  get f(){
    const respuesta = this.formularioNewTike;
    return respuesta.controls;
  }

  newTicket(){
    this.router.navigate(['/tiket/listatickets']);
  }

  guardar(){
    this.ticketservice.createMessageTicket(this.tiket)
    .subscribe(respuesta=>
      {
        var userResponse: RegisterResponse = respuesta;
        if(userResponse != null && userResponse.Exitoso)
        {
          this.mensaje = [{ id:2, type:'success', message: "Sumensaje se ha enviado exitosamente" }];
          this.tiket = {};
          this.router.navigate(['/tiket/listatickets']);
        }
        else{
            this.mensaje = [{ id:2, type:'warning', message: userResponse.MensajeResultado }];
        }
      },error=>{
        this.mensaje = [{
           id:2,
           type:'danger',
           message:error.statusText
        }];
      });


  }
}
