import { Component, OnInit } from "@angular/core";
import { ticketMessage } from "src/app/modelos/tickectMessage.model";
import { GenericResponse } from "src/app/modelos/Response/GenericResponse.model";
import { Observable } from "rxjs";
import { TicketService } from "../../services/ticket.service";
import { ActivatedRoute, Router } from "@angular/router";
import { FormGroup, FormBuilder, Validators,AbstractControl } from "@angular/forms";
import { Ticket } from 'src/app/modelos/Ticket.model';
import { TicketResponse } from 'src/app/modelos/Response/TicketResponse.model';
import { RegisterResponse } from 'src/app/modelos/Response/RegisterResponse.model';
import { StorageServiceService } from 'src/app/services/storage-service.service';

@Component({
  selector: "app-detalle",
  templateUrl: "./detalle.component.html",
  styleUrls: ["./detalle.component.css"]
})
export class DetalleComponent implements OnInit 
{ 
  listaMessage: ticketMessage[];
  busqueda: ticketMessage = new ticketMessage();
  InfoMensaje: ticketMessage = new ticketMessage();
  nombreUsuarioActual : string;
  
  tikect: Ticket = new Ticket();
  tiketresponse: TicketResponse;
  
  mensaje: any = [];
  isSpinnerVisible = false;
  isLoadingVisible = true;
  formularioRespuestaTike: FormGroup;

  constructor(
    private localStorage : StorageServiceService,
    private tikectservi: TicketService,
    private route: ActivatedRoute,
    private routers:Router,
    public fb: FormBuilder) 
  {
    this.nombreUsuarioActual = this.localStorage.getCurrentUser().Login;
  }

  ngOnInit() 
  {
    var idTicket = this.route.snapshot.params["IdMensaje"];
    this.busqueda.IdTicket = idTicket;
    this.tikect.IdTicket = idTicket;

    this.tikectservi.consultarTicket(this.tikect).subscribe(respues=>
    {
      this.isLoadingVisible = false;
      this.tiketresponse = respues.JSonResult[0];
    },
    error=>
    {
        this.mensaje = [{ id:1, type:'danger', message: "Se ha presentado un error al recuperar su información." }];
        this.isLoadingVisible = false;
    });

    this.tikectservi.consultarMessageTicket(this.busqueda).subscribe(respuesta => {
      this.listaMessage = respuesta.JSonResult;
      this.busqueda = this.listaMessage[0];
      this.isLoadingVisible = false;
    },
    error=>
    {
        this.mensaje = [{ id:1, type:'danger', message: "Se ha presentado un error al recuperar su información." }];
        this.isLoadingVisible = false;
    });

    this.isSpinnerVisible = false;
    this.formularioRespuestaTike = this.fb.group({
      Body : ['', Validators.compose([Validators.required, Validators.maxLength(100)])],
    });
  }

  get f(){
    const respuesta = this.formularioRespuestaTike;
    return respuesta.controls;
  }

  guardar() 
  {
      this.InfoMensaje.IdTicket = this.tikect.IdTicket;
      this.InfoMensaje.Subject = this.busqueda.Subject;
      this.isSpinnerVisible = true;

      this.tikectservi.createMessageTicket(this.InfoMensaje).subscribe(respuesta=>
      {
        this.isSpinnerVisible = false;
        var registroResponse: RegisterResponse = respuesta;
        if(registroResponse != null && registroResponse.Exitoso)
        {
            this.InfoMensaje.IdUser = this.nombreUsuarioActual;
            this.listaMessage.push(this.InfoMensaje);
            this.InfoMensaje = new ticketMessage();
        }
        else
        {
            this.mensaje = [{ id:2, type:'warning', message: registroResponse.MensajeResultado }];
        }
      },
      error=>
      {
        this.isSpinnerVisible = false;
        this.mensaje = [{ id:2, type:'danger', message:error.statusText }];
      });
  }

  public Regresar()
  {
      this.routers.navigate(["/tiket/listatickets"]);
  }
}
