import { Ticket } from './../../modelos/Ticket.model';
import { TicketResponse } from './../../modelos/Response/TicketResponse.model';
import { GenericResponse } from '../../modelos/Response/GenericResponse.model';
import { Component, OnInit } from '@angular/core';
import {  Router } from '@angular/router';

import { TicketService } from '../../services/ticket.service';

import { Observable } from 'rxjs';


@Component({
  selector: 'app-listatickets',
  templateUrl: './listatickets.component.html',
  styleUrls: ['./listatickets.component.css']
})
export class ListaticketsComponent implements OnInit {
  listaticket:TicketResponse[];
  busqueda:Ticket = new Ticket();
  resultsConsultaTiket$:Observable<GenericResponse<TicketResponse>>;
  page = 1;
  pageSize = 2;

  constructor(private router: Router,
    public tiketservi:TicketService
    ) {}


  ngOnInit() {

    this.resultsConsultaTiket$ = this.tiketservi.consultarTicket(this.busqueda);
    this.resultsConsultaTiket$.subscribe(respuesta => {this.listaticket = respuesta.JSonResult
      this.listaticket.slice((this.page - 1) * this.pageSize, (this.page - 1) * this.pageSize + this.pageSize);
    });

  }

  newTicket() {
    this.router.navigate(['/tiket/nuevoticket']);
  }



}
