import { TiketRoutes } from '../ticket/tiket.routing';
import { debounceTime } from 'rxjs/operators';
import { TicketResponse } from 'src/app/modelos/Response/TicketResponse.model';
import { Injectable } from '@angular/core';

import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import { StorageServiceService } from './storage-service.service';
import { RegisterResponse } from '../modelos/Response/RegisterResponse.model';
import { GenericResponse } from '../modelos/Response/GenericResponse.model';
import { Ticket } from '../modelos/Ticket.model';
import { ticketMessage } from '../modelos/tickectMessage.model';
import {UrlBackendServices} from './AppServiceGlobal';

@Injectable({
  providedIn: 'root'
})
export class TicketService {

  private ulrl = UrlBackendServices + '/api';
  constructor(private http: HttpClient, private storageServ: StorageServiceService) { }

  createMessageTicket(tiket: any): Observable<RegisterResponse> 
  {
    const headers = new HttpHeaders({ 'Content-Type': 'application/json', 'Access-Control-Allow-Origin': '*' });
    const newtiket = {
      ApplicationId: this.storageServ.getCurrentApplication(),
      IdUser         : this.storageServ.getCurrentUser().IdUser,
      Token : this.storageServ.getCurrentToken(),
      Subject: tiket.Subject,
      Body: tiket.Body,
      IdTicketType: tiket.IdTicketType,
      IdTicket: tiket.IdTicket
    };

    return this.http.put<RegisterResponse>(this.ulrl + '/MessageTicket', newtiket, { headers: headers });
  }



  consultarTicket(ticket: Ticket): Observable<GenericResponse<TicketResponse>> {
    const headers = new HttpHeaders({ 'Content-Type': 'application/json', 'Access-Control-Allow-Origin': '*' });
    ticket.IdUserCreation = ticket.IdUserCreation != null ? ticket.IdUserCreation : this.storageServ.getCurrentUser().IdUser
    const consulta = {
      Parameters: ticket,
      ApplicationId: this.storageServ.getCurrentApplication(),
      Token : this.storageServ.getCurrentToken()
    };
    return this.http.post<GenericResponse<TicketResponse>>(this.ulrl + '/Ticket', consulta, { headers: headers });
  }

  consultarMessageTicket(ticket: ticketMessage): Observable<GenericResponse<ticketMessage>>{
    const headers = new HttpHeaders({ 'Content-Type': 'application/json', 'Access-Control-Allow-Origin': '*' });
    const consulta = {
      ApplicationId: this.storageServ.getCurrentApplication(),
      Token : this.storageServ.getCurrentToken(),
      IdUser: this.storageServ.getCurrentUser().IdUser,
      IdTicket: ticket.IdTicket
    };

    return this.http.post<GenericResponse<ticketMessage>>(this.ulrl + '/MessageTicket', consulta, { headers: headers });
  }

}
