import { Routes } from '@angular/router';
import { NuevoticketComponent } from './nuevoticket/nuevoticket.component';
import { ListaticketsComponent } from './listatickets/listatickets.component';
import { DetalleComponent } from './detalle/detalle.component';







export const TiketRoutes: Routes = [
  {
    path: '',
    children: [
      {
        path: 'nuevoticket',
        component: NuevoticketComponent
      },
      {
        path: 'listatickets',
        component: ListaticketsComponent
      },
      {
        path: 'detalle/:IdMensaje',
        component: DetalleComponent
      }
    ]
  }];

