import { QuillModule } from 'ngx-quill';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { NuevoticketComponent } from './nuevoticket/nuevoticket.component';
import { ListaticketsComponent } from './listatickets/listatickets.component';
import { DetalleComponent } from './detalle/detalle.component';
import { RouterModule } from '@angular/router';
import { TiketRoutes } from './tiket.routing';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';

import { StorageServiceService } from '../services/storage-service.service';
import { UtilidadesModule } from '../utilidades/utilidades.module';
import { TicketService } from '../services/ticket.service';

@NgModule({
  declarations: [NuevoticketComponent, ListaticketsComponent, DetalleComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(TiketRoutes),
    FormsModule,
    NgbModule,
    ReactiveFormsModule,
    QuillModule,
    UtilidadesModule
  ],
  providers: [TicketService, StorageServiceService]
})
export class TicketModule { }
